:root{
    --greenHack: rgb(48, 203, 48);
}

.ComputerDate{
    background-image: url("../img/achraflogo.jpg");
    height: 400px;
    width: 550px;
    background-repeat: no-repeat;
    text-align: center;
    color: var(--greenHack);
    font-weight: bold;
}

.info-in-pc{
    position: relative;
    bottom: -125px;
}

.global{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.title{
    border: 10px solid rgb(250, 209, 134);
    border-radius: 30px;
    background-color: rgb(255, 136, 0);
    color: white;
    padding: 10px;
}

.content-name{
    border: 10px solid rgb(196, 250, 134);
    border-radius: 30px;
    width: 527px;
    height: 100px;
    background-color: var(--greenHack);
    color: rgb(0, 0, 0);
    font-weight: bold;
}

.signature{    
    padding: 20px;
}

.TGGOWNER{
    display: flex;
    justify-content: end;
}